import React, { Component } from 'react';

class Estructura extends Component {
    constructor(props) {
        super(props);

        console.log('Estructura -> constructor')
    }

    /* const [mostrar, setMostrar] = useState(true) */

    state = {
        mostrar: true,
        personas: [
            'Hernan',
            'Mariano',
            'Facundo',
            'Erika',
            'Nahuel'
        ],
        alumnos: [
            { nombre: 'Hernan', apellido: 'Bruno', edad: 25, curso: true, foto: 'https://cdn3.iconfinder.com/data/icons/business-avatar-1/512/3_avatar-256.png'},
            { nombre: 'Mariano', apellido: 'Ferle', edad: 23, curso: false, foto: 'https://cdn3.iconfinder.com/data/icons/business-avatar-1/512/10_avatar-256.png'},
            { nombre: 'Santiago', apellido: 'Cardone', edad: 22, curso: true, foto: 'https://cdn3.iconfinder.com/data/icons/business-avatar-1/512/8_avatar-256.png'},
            { nombre: 'Nahuel', apellido: 'Moreno', edad: 26, curso: false, foto: 'https://cdn3.iconfinder.com/data/icons/business-avatar-1/512/7_avatar-256.png'},
        ]
    }

    cambiar() {
        console.log('cambiar')

        // this.setState({ mostrar: !this.state.mostrar }) // se usa más
        this.setState(prevstate => ({ mostrar: !prevstate.mostrar }))
    }
    render() {
        let { mostrar, personas, alumnos } = this.state

        return (
            <div className="estructura">
                <div className="bg-primary bg-opacity-25 rounded-3 mt-3 p-5 mb-5">
                    <h3>Componente Estructura</h3>
                    <hr />

                    {/* --------------------------------------------------------- */}
                    <h4><u>Rendering Condicional</u></h4>

                    <button className="btn btn-primary my-2" onClick={() => this.cambiar()}>
                        {/* 0, {}, [], null, undefined, "", NaN => falsy */}
                        { mostrar ? 'Ocultar' : 'Mostrar' }
                    </button>


                    { mostrar ? 
                            <p className="alert alert-info">
                                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aspernatur ab quisquam culpa ducimus nulla quaerat corrupti fugit cumque. Fuga ab velit magnam sint laboriosam corrupti quisquam! Quia nam corrupti eaque!
                            </p>
                        :
                            <p className="alert alert-danger">
                                Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aspernatur ab quisquam culpa ducimus nulla quaerat corrupti fugit cumque. Fuga ab velit magnam sint laboriosam corrupti quisquam! Quia nam corrupti eaque!
                            </p>
                    }

                    { mostrar && 
                        <p className="alert alert-info">
                            Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aspernatur ab quisquam culpa ducimus nulla quaerat corrupti fugit cumque. Fuga ab velit magnam sint laboriosam corrupti quisquam! Quia nam corrupti eaque!
                        </p>
                    }
                    <hr />

                    {/* --------------------------------------------------------- */}
                    <h4 className="mb-4"><u>Loop Rendering</u></h4>

                    <h5>Array de strings en una lista</h5>
                    {/* <ul>
                        <li>{personas[0]}</li>
                        <li>{personas[1]}</li>
                        <li>{personas[2]}</li>
                        <li>{personas[3]}</li>
                        <li>{personas[4]}</li>
                    </ul> */}

                    <ul>
                        { personas 
                            && personas.map( (persona, indice) => <li key={indice}>{`${indice + 1} - ${persona}`}</li>)
                        }
                    </ul>
                    
                    <h5>Array de strings en una lista</h5>
                    <ul>
                        {
                            alumnos.map((alumno, indice) => {
                                indice = indice + 1 // Esto no debería estar
                                const { nombre, apellido } = alumno  // Esto no debería estar
                                return <li key={indice}> {indice } - {nombre} {apellido}</li>
                            })
                        }

                    </ul>
                    
                    <ul>
                    {
                            alumnos.map((alumno, indice) => {
                                return <li key={indice + 1}> {indice + 1} - {alumno.nombre} {alumno.apellido}</li>
                            })
                        }
                    </ul>

                    <h5>Array de objetos en una tabla</h5>

                    <div className="table-responsive">
                        <table className="table table-dark">
                            <thead>
                                <tr>
                                    <th>Nombre</th>
                                    <th>Apellido</th>
                                    <th>Edad</th>
                                    <th>Curso</th>
                                    <th>Foto</th>
                                </tr>
                            </thead>
                            <tbody>
                                {
                                    /* Acá muestro todos los alumnos */
                                   /*  alumnos.map((alumno, indice) => ( */

                                   /* Acá filtro los alumnos que SI hicieron el curso */
                                    /* alumnos.filter(alumno => alumno.curso).map( (alumno, indice ) => ( */
                                    
                                    /* Acá filtro los alumnos que NO hicieron el curso */
                                    alumnos.filter(alumno => !alumno.curso).map( (alumno, indice ) => (
                                        <tr key={indice}>
                                            <td>{alumno.nombre}</td>
                                            <td>{alumno.apellido}</td>
                                            <td>{alumno.edad}</td>
                                            <td>{alumno.curso ? 'Si': 'No'}</td>
                                            <td>
                                                <img src={alumno.foto} width="50" alt={alumno.nombre} />
                                            </td>
                                        </tr>
                                    ))
                                }
                            </tbody>
                        </table>
                    </div>

                </div>
            </div>
        );
    }
}

export default Estructura;