import React from 'react';
import { NavLink } from 'react-router-dom';

const linkActivo = () => (
    {
        color: 'yellow',
        fontWeight: 'bold'
    }
)

const Navbar = () => {
    return (
        <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
            <div className="container-fluid">
                <NavLink className="navbar-brand" to="/">Inicio</NavLink>
                <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarNav">
                    <ul className="navbar-nav">
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/entrada" activeStyle={linkActivo()}>Entrada</NavLink>
                        </li>
                        <li className="nav-item">
                            <NavLink className="nav-link" to="/mensaje-4/Mensaje-Nro-4/green" activeStyle={linkActivo()}>Mensaje 4</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/mensaje-5/Mensaje-Nro-5/pink" activeStyle={linkActivo()}>Mensaje 5</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/mensaje-6/Mensaje-Nro-6/lightblue" activeStyle={{ color: 'yellow', fontWeight: 'bold' }}>Mensaje 6</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/estructura" activeStyle={{ color: 'yellow', fontWeight: 'bold' }}>Estructura</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/formulario" activeStyle={{ color: 'yellow', fontWeight: 'bold' }}>Formulario</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/fdasfs" activeStyle={{ color: 'yellow', fontWeight: 'bold' }}>No Valida 1</NavLink>
                        </li>

                        <li className="nav-item">
                            <NavLink className="nav-link" to="/3213213" activeStyle={{ color: 'yellow', fontWeight: 'bold' }}>No Valida 2</NavLink>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
    )
}

export default Navbar;


