import React, {Component} from 'react';

class Entrada extends Component {
    constructor(props) {
        super(props);

        console.log('Entrada -> construtor')
    }
    state = { 
        valor1: 123
     }

    /* ---------------------------------------------------------- */
    /* LIFE CYCLE HOOKS                                           */
    /* ---------------------------------------------------------- */
    // se invoca inmediatamente después de que un componente se monte (se inserte en el árbol). La inicialización que requiere nodos DOM debería ir aquí. Si necesita cargar datos desde un punto final remoto, este es un buen lugar para instanciar la solicitud de red.
    componentDidMount() {
        console.log('Entrada -> componentDidMount')
        this.refTimer = setInterval(() => {
            console.log('Entró el interval', new Date().toLocaleString())
            this.setState( prevstate => ({valor1: prevstate.valor1 + 1}))
        }, 1000)
    }

    // se invoca inmediatamente después de que la actualización ocurra. Este método no es llamado para el renderizador inicial.
    componentDidUpdate() {
        console.log('Entrada -> componentDidUpdate')
    }
    // se invoca inmediatamente antes de desmontar y destruir un componente. Realiza las tareas de limpieza necesarias en este método, como la invalidación de temporizadores, la cancelación de solicitudes de red o la eliminación de las suscripciones que se crearon en componentDidMount()
    componentWillUnmount() {
        console.log('Entrada -> componentWillUnmount')
        clearInterval(this.refTimer)
    }

    render() { 
        return (
            <div className="entrada">
                <div className="bg-primary bg-opacity-25 rounded-3 mt-3 p-5 mb-5">
                    <h3>Componente Entrada</h3>
                    <hr />
                    <p>Ciclos de vida</p>
                </div>
            </div>
        );
    }
} 
export default Entrada;