import React from 'react'
import { BrowserRouter, Route, Switch, Redirect } from 'react-router-dom'

import './App.css';

import Contador from './components/Contador';
import Entrada from './components/Entrada';
import Estructura from './components/Estructura';
import Formulario from './components/Formulario';
import Mensaje from './components/Mensaje';
import Multiplicador from './components/Multiplicador';
import Navbar from './components/Navbar';
import RutaNoValida from './components/RutaNoValida';
/* import Button from './components/Button'; */

const App = () => {
  return (
    <div className="App">
      <header className="container mt-3">
        <div className="jumbotron">
          <h1>Mi proyecto en React.JS!</h1>
          <hr />
          <p>Lorem, ipsum dolor sit amet consectetur adipisicing elit. Eveniet aspernatur aut iste placeat aliquam corrupti sunt sit molestiae quia iusto quae repellat laborum facilis ratione quis tenetur necessitatibus, optio porro!</p>

          <div className="row">
            <div className="col-4">
              <Mensaje msj="Hola soy el 1er mensaje" color="red" />

            </div>
            <div className="col-4">
              <Mensaje msj="Hola soy el 2do mensaje" color="gold" />

            </div>
            <div className="col-4">
              <Mensaje msj="Hola soy el 2do mensaje" color="lightblue" />
            </div>
          </div>


          <h2 className="mb-3"><u>Router</u></h2>

          <BrowserRouter>
            <Navbar />
            <Switch>

              <Route exact path="/">
                <Redirect to="/entrada" />
              </Route>

              <Route path="/entrada" component={() => <Entrada />}></Route>
              <Route path="/estructura" component={() => <Estructura />}></Route>
              <Route path="/formulario" component={() => <Formulario />}></Route>

              {/* Ruteamos componentes con parámetros por props */}
              {/* <Route path="/mensaje-4" component={() => <Mensaje msj="Menasje Nro. 4" color="violet" />}></Route>
              <Route path="/mensaje-5" component={() => <Mensaje msj="Menasje Nro. 5" color="lightblue" />}></Route>
              <Route path="/mensaje-6" component={() => <Mensaje msj="Menasje Nro. 6" color="olive" />}></Route> */}
            
              {/* Ruteamos componentes con parámetros por la ruta */}
              <Route path="/mensaje-4/:msj/:color" component={Mensaje}></Route>
              <Route path="/mensaje-5/:msj/:color" component={Mensaje}></Route>
              <Route path="/mensaje-6/:msj/:color" component={Mensaje}></Route>
              
              {/* Ruteamos componentes apara rutas no válidas */}
              <Route component={RutaNoValida}></Route>
            </Switch>
          </BrowserRouter>


          {/* -----------------Multiplicador----------------- */}

          <div className="row">
            <div className="col-12">
              <Multiplicador />
            </div>
          </div>

          {/* -----------------CONTADOR----------------- */}

          <div className="row">
            <div className="col-12">
              <Contador id="1" valor={123} fondo="pink" />
            </div>
            <div className="col-12">
              <Contador id="2" valor={456} fondo="blue" />
            </div>
            <div className="col-12">
              <Contador id="3" valor={789} fondo="deeppink" />
            </div>
          </div>


          {/* <Button /> */}
        </div>
      </header>
    </div>
  );
}

export default App;
