import React from 'react';

class Formulario extends React.Component {
    constructor(props) {
        super(props);

        console.log('Formulario -> Constructor')
    }
    state = { 
        nombre: '',
        apellido: '',
        edad: '',
        email: '',
        password: '',
        notas: ''
     }

     enviar(e) {
         e.preventDefault()

         console.log(this.state)

         // Limpio el formulario
         this.setState({ 
            nombre: '',
            apellido: '',
            edad: '',
            email: '',
            password: '',
            notas: ''
         })

     }

    render() { 

        let {nombre, apellido, edad, email, password, notas} = this.state

        return ( 
            <div className="formulario">
                <div className="bg-danger bg-opacity-25 rounded-3 mt-3 p-5 mb-5">
                    <h3>Componente Formulario</h3>
                    <hr />

                    <form autoComplete='off' onSubmit={e => this.enviar(e)}>

                        {/* ------------------- CAMPO NOMBRE -------------------  */}
                        <div className="form-group">
                            <label htmlFor="nombre">Nombre</label>
                            <input type="text" id="nombre" className="form-control" value={nombre} onChange={ e => this.setState({ nombre: e.target.value})}/>
                        </div>

                        {/* ------------------- CAMPO APELLIDO -------------------  */}
                        <div className="form-group">
                            <label htmlFor="apellido">Apellido</label>
                            <input type="text" id="apellido" className="form-control" value={apellido} onChange={ e => this.setState({ apellido: e.target.value})}/>
                        </div>

                        {/* ------------------- CAMPO EDAD -------------------  */}
                        <div className="form-group">
                            <label htmlFor="edad">Edad</label>
                            <input type="text" id="edad" className="form-control" value={edad} onChange={ e => this.setState({ edad: e.target.value})}/>
                        </div>
                        
                        {/* ------------------- CAMPO EMAIL -------------------  */}
                        <div className="form-group">
                            <label htmlFor="email">Email</label>
                            <input type="email" id="email" className="form-control" value={email} onChange={ e => this.setState({ email: e.target.value})}/>
                        </div>

                        {/* ------------------- CAMPO PASSWORD -------------------  */}
                        <div className="form-group">
                            <label htmlFor="password">Password</label>
                            <input type="password" id="password" className="form-control" value={password} onChange={ e => this.setState({ password: e.target.value})}/>
                        </div>

                        {/* ------------------- CAMPO NOTAS -------------------  */}
                        <div className="form-group">
                            <label htmlFor="notas">Notas</label>
                            <textarea type="text" id="notas" rows="10" cols="30" className="form-control" value={notas} onChange={ e => this.setState({ notas: e.target.value})}/>
                        </div>

                        {/* ------------------- CAMPO ENVÍO -------------------  */}
                        
                        <button className="btn btn-success mt-3">Enviar</button>

                    </form>

                </div>
            </div>
         );
    }
}
 
export default Formulario;