// import React, { Fragment } from 'react'
import React, { useState } from 'react'
import P from './P'

const Multiplicador = () => {

    /*  console.log(useState(1)[0])
     console.log(useState(1)[1])
  */
    // hook (gancho) useState
    const [multiplicador, setMultiplicador] = useState(1)

    const multi = () => {
        setMultiplicador(multiplicador * 2)
    }

    return (
        <>

            <div className="mensaje">
                <div className="bg-primary bg-opacity-25 rounded-3 mt-3 p-5 mb-5">
                    <h2>Multiplicador: {multiplicador}</h2>
                    <hr />

                    <P numero="1">
                        Hola soy un parrafo del <strong>multiplicador</strong>
                    </P>

                    <P numero="2">
                        Soy otro parrafo del <strong>multiplicador</strong>
                    </P>

                    <button onClick={multi} className="btn btn-primary mt-4">Multiplicar</button>
                </div>
            </div>
        </>
    );
}

export default Multiplicador;