import React from 'react'
import moment from 'moment'
import './Mensaje.css'

/* ----------------------------------------------------------------------------- */
/* Componente React basado en una función (no guarda estado -> stateless)        */
/* ----------------------------------------------------------------------------- */

// function Mensaje() { // JS5
// const Mensaje = () => { // ES6 (Arrow function o función flecha o función lambda)
// const Mensaje = function(props) { //JS5
const Mensaje = function(props) { //JS5 {msj, color}
    console.log(props)

    const { msj, color } = props.match?.params || props // Optional Chaining  (?.) // Short Ciruit Operator (||)

    /* function sumarUno() {
        let contador = 0
        return ++contador
    } */

    return (
        <div className="mensaje">
            <div className="bg-opacity-25 rounded-3 mt-3 p-5 mb-5" style={{backgroundColor: color}}>
                <h1>{ msj }</h1>
                <p> { moment().format('MMMM Do YYYY, h:mm:ss a') } </p>
                <hr />
                <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Consequuntur placeat cumque maxime recusandae repellendus alias eius, soluta beatae veritatis nemo quo repudiandae, provident, explicabo in! Quo animi nihil laboriosam inventore.</p>

                {/* { sumarUno() } <br /> */}
                {/* { 4 + 3 } <br /> */}

                {/*  { new Date().toLocaleString() } <br /> */}
                {/* { {x: 1, x: 2} } */} {/* NO VALIDO */}
                {/* { [2, 4, 6] } */}

            </div>
        </div>
    );

}
 
export default Mensaje;