import React, {Component} from 'react';

import './Contador'

/* ----------------------------------------------------------------- */
/* Componente React basado en una clase (guarda estado -> statefull) */
/* ----------------------------------------------------------------- */

class Contador extends Component {
// class Contador extends React.Component {
    constructor(props) {
        super(props); // props, obligatorio si uso constructor

        /* this.incrementar = this.incrementar.bind(this) */

        console.log('Constructor del Contador')
    }
    
    state = { 
        /* contador: 123, */
        /* mensaje = "" */
        contador: this.props.valor
    }

    incrementar() {
        let {id} = this.props
        /* ----------------------------------------------------------------------- */
        /* NO es la forma de actualizar una propiedad en el estado del componente  */
        /* ----------------------------------------------------------------------- */
        /* this.state.contador = this.state.contador + 1
        console.log(this.state.contador)
        this.forceUpdate() */

        /* Así SI!!!! */
        /* this.setState({ contador: 777 }) */
        this.setState({ contador: this.state.contador + 1 })
        this.setState( prevstate => ({ contador: prevstate.contador + 1}), () => {
            console.log(this.state.contador)
            console.log(`Contador ${id} Incrementar: ${this.state.contador}`)
        })
    }

    render() {
        let { id, fondo } = this.props // desestructuramos las props
        let { contador } = this.state

        return (
            <div className="mensaje">
                <div className="bg-opacity-25 rounded-3 mt-3 p-5 mb-5" style={{backgroundColor: fondo}}>
                    <h3>Contador Nro. { id }</h3>
                    {/* <h1>{this.props.valor}</h1>  */}

                    <h4 className="alert alert-info">
                        Contador: { contador }
                    </h4>

                    {/*  <button className="btn btn-success" onClick={this.incrementar}> */}
                    <button className="btn btn-success" onClick={() => this.incrementar()}>
                        Incrementar
                    </button>
            
                </div>
            </div>
        );
    }
}
 
export default Contador;